<template>
    <div class="container" v-loading.fullscreen.lock="loading" element-loading-background="rgba(0, 0, 0, 0.8)">
        <el-row class="title MT-30" :gutter="30">
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" >
                <div class="title-item MB-30 flex-row-center">
                    <img src="~@/assets/images/presale/purchase.png" class="title-item__pic MR-30"/>
                    <div class="text-left">
                        <div class="text-20 text-white MB-15">Total purchase</div>
                        <div class="text-26 text-yellow text-bold">￥{{purchase.token || 0}}</div>
                    </div>
                </div>
            </el-col>
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
                <div class="title-item MB-30 flex-row-center">
                    <img src="~@/assets/images/presale/commission.png" class="title-item__pic title-item__commission MR-30"/>
                    <div class="text-left">
                        <div class="text-20 text-white MB-15">Personal Commission</div>
                        <div class="text-26 text-commission text-bold">￥{{purchase.usd || 0}}</div>
                    </div>
                </div>
            </el-col>
        </el-row>

        <div class="tab PB-40">
            <div class="tab-list text-26 text-bold flex-row-center text-white" :class="{'two': tabIndex == 1}">
                <div class="tab-list__item pointer" :class="{'text-yellow': tabIndex == 0}" @click="toggleTab(0)">Invitation</div>
                <div class="tab-list__item pointer" :class="{'text-yellow': tabIndex == 1}" @click="toggleTab(1)">Invitation list</div>
            </div>
            <div class="tab-content MT-40" v-show="tabIndex == 0">
                <div class="tab-content__title flex-row-start">
                    <div class="tab-list__one PL-10 PR-10">Invitation address</div>
                    <div class="tab-list__two">Profit</div>
                </div>
                <div class="tab-content__list text-white text-16">
                    <div class="tab-content__item flex-row-start" v-for="item in tableList" :key="item">
                        <div class="tab-list__one break-word PL-10 PR-10">{{item.address}}</div>
                        <div class="tab-list__two">${{item.profit}}</div>
                    </div>
                    <el-empty v-if="!tableList || tableList.length <= 0" :image-size="200"></el-empty>
                </div>
                
                    <el-pagination class="MT-10" v-model:current-page="currentPage" background layout="prev, pager, next" :total="totalNumber" :pager-count="10" :page-size="limit" @current-change="pageChange"></el-pagination>
            </div>
            <!-- <List v-show="tabIndex == 1" @add="addUser" :list="invitationList"/> -->
            <div class="tab-content MT-40" v-show="tabIndex == 1">
                <div class="tab-content__title flex-row-start">
                    <div class="tab-list__one PL-10 PR-10">Invitation address</div>
                    <div class="tab-list__two">Profit</div>
                </div>
                <div class="tab-content__list text-white text-16">
                    <div class="tab-content__item flex-row-start" v-for="(item, index) in invitationList" :key="index">
                        <div class="tab-list__one break-word PL-10 PR-10">{{item.address}}</div>
                        <div class="tab-list__two">
                            <span class="text-16 text-yellow" v-if="item.status == 0">Testing</span>
                            <span class="text-16 text-red" v-if="item.status == -1">Detection failed</span>
                            <el-icon color="#52C41A" class="ML-10" v-if="item.status == 1"><icon-select /></el-icon>
                        </div>
                    </div>
                    <div v-loading="addLoading" element-loading-background="rgba(0, 0, 0, 0.8)">
                        <div class="tab-user PT-30 PB-30">
                            <div class="text-16 text-bold text-white text-left MB-20">Users</div>
                            <div class="tab-user__text">
                                <textarea v-model.trim="userInfo"></textarea>
                            </div>
                            <div class="flex-row-between text-16 MT-20">
                                <div class="tab-user__btn tab-user__add pointer" @click="addUser">Add usders</div>
                                <div class="tab-user__btn tab-user__invitation pointer" @click="invitationHandler">invitation</div>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import List from './components/list.vue'
import {Select } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import { utils } from 'ethers'
export default {
    components: {
        List,
        IconSelect: Select
    },
    data() {
        return {
            // 当前tab
            tabIndex: 0,
            // 已邀请列表
            tableList: [],
            // 初始化loading
            loading: false,
            // 页面数据
            purchase: {},
            // 当前邀请列表
            invitationList: [],
            userInfo: '',
            addLoading: false,
            // 从哪里开始
            from: 0,
            // 每页多少条
            limit: 10,
            // 个人佣金
            personal: 0,
            // 总佣金
            total: 0,
            // 总条数
            totalNumber: 0,
            // 当前页数
            currentPage: 1
        }
    },
    mounted() {
        this.getInvitation()

        this.$wallet.on('accountChange', this.accountChange)
    },
    methods: {
        // 页数改变
        pageChange() {
            this.getInvitation()
        },
        // 账号变化
        accountChange() {
            console.log(this.$wallet.accounts)
            if (this.$wallet.accounts.length > 0) {
                this.getInvitation()
            } else {
                this.personal = 0,
                this.total = 0,
                this.totalNumber = 0,
                this.currentPage = 1
                this.tableList = []
                this.loading = false
                this.purchase = {}
                this.invitationList = []
                this.userInfo = ''
            }
        },
        // 添加地址
        async addUser() {
            if (!this.userInfo) {
                ElNotification({
                    type: 'error',
                    title: 'Tips',
                    message: 'The list is empty',
                })
                return
            }
            const userList = this.userInfo.split('\n')
            this.userInfo = ''
            try {
                const list = userList.map(item => {
                    let res = {}
                    res.status = 0
                    res.address = item
                    return res
                })
                this.invitationList = [...this.invitationList, ...list]
                this.checkUser()
            } catch (error) {
                ElNotification({
                    title: 'Error',
                    message: error,
                    type: 'error',
                })
            }
        },
        // 检测地址
        async checkUser() {
            const network = await this.$wallet.initNetwork()
            const { scoutDogeABI} = network
            this.invitationList.forEach(item => {
                scoutDogeABI.estimateGas.manyInvitePlayers([item.address]).then(data => {
                    item.status = 1
                }).catch(() => {
                    item.status = -1
                })
            });
        },
        // 邀请当前输入列表
        async invitationHandler() {
            if (this.invitationList.length <= 0) {
                ElNotification({
                    title: 'Tips',
                    message: 'The current invitation list is empty',
                    type: 'error',
                })
                return
            }
            let isEnd = true
            let list = []
            for (let index = 0; index < this.invitationList.length; index++) {
                const item = this.invitationList[index];
                if (item.status == 0) {
                    isEnd = false
                    break;
                }
                if (item.status == 1) {
                    list.push(item.address)
                }
            }
            if (list.length <= 0) {
                ElNotification({
                    title: 'Tips',
                    message: 'The current invitation list is empty',
                    type: 'error',
                })
                return
            }
            // 如果当前检测未结束，则停止
            if (!isEnd) {
                ElNotification({
                    title: 'Tips',
                    message: 'Testing, please wait...',
                    type: 'error',
                })
                return 
            }
            this.addLoading = true
            try {
                const network = await this.$wallet.initNetwork()
                const { scoutDogeABI} = network
                const tx = await scoutDogeABI.manyInvitePlayers(list)
                await tx.wait()
                this.addLoading = false
                this.tabIndex = 0
                this.currentPage = 1
                this.invitationList = []
                ElNotification({
                    title: 'Tips',
                    message: 'Invitation succeeded',
                    type: 'success',
                })
                this.getInvitation()
            } catch (error) {
                ElNotification({
                    title: 'Error',
                    message: error,
                    type: 'error',
                })
                this.addLoading = false
            }
        },
        // 获取邀请列表
        async getInvitation() {
            this.loading = true
            try {
                const network = await this.$wallet.initNetwork()
                const { scoutDogeABI, accounts} = network
                const tx = await scoutDogeABI.getSubordinateSize(accounts[0])
                const total = await scoutDogeABI._totalPurchase()
                const personal = await scoutDogeABI._purchase(accounts[0])
                this.personal = utils.formatUnits(personal.token, 9) * 0.02
                this.total = utils.formatUnits(total.token, 9)* 0.02
                let number = tx.toNumber()
                this.totalNumber = number
                if (number > 0) {
                    const offset = (this.currentPage - 1) * this.limit
                    let limit = this.limit
                    // 如果当前每页条数+起始数大于总条数，则limit要修正
                    if (this.limit + offset > number) {
                        limit = number - offset
                    }
                    const res = await scoutDogeABI.getSubordinate(offset, limit)
                    this.tableList = res.map(item => {
                        let data = {}
                        data.address = item
                        data.profit = 'wait...'
                        return data
                    });
                    this.searchAddressProfit()
                }
                this.loading = false
            } catch (error) {
                this.loading = false
                ElNotification({
                    title: 'Error',
                    message: error,
                    type: 'error',
                })
            }
        },
        // 查询邀请地址收益
        searchAddressProfit() {
            this.tableList.forEach(item => {
                this.$wallet.scoutDogeABI._purchase(item.address).then(data => {
                    item.profit = utils.formatUnits(data.token, 9)* 0.02
                }).catch(() => {
                    item.profit = 'error'
                })
            })
        },
        toggleTab(index) {
            this.tabIndex = index
        }
    }
}
</script>

<style lang="scss">
.el-empty__image{
    opacity: .5 !important;
}
</style>
<style lang="scss">
    .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
        background: transparent !important;
        border: 1px solid #545559 !important;
        line-height: 24px !important;
        color: #fff;
    }
    .el-pagination.is-background .el-pager li:not(.disabled).active{
        border-color: #F4A801 !important;
        color: #F4A801 !important;
    }
    .el-pagination.is-background .el-pager li:not(.disabled):hover{
        color: #F4A801 !important;
    }
    .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev{
        color: #fff;
    }
    .el-pagination.is-background .btn-next:disabled, .el-pagination.is-background .btn-prev:disabled{
        color: #606266 !important;
    }
</style>
<style lang="scss" scoped>
@media only screen and (max-width: 768px){
    .container{
        padding: 0 .3rem !important;
    }
}
