    background: #101529;
    padding: 0 15%;
    overflow: hidden;
    .title-item{
        height: 1.8rem;
        background: linear-gradient(0deg, #141930, #101529);
        border: 1px solid #545559;
        border-radius: .21rem;
        .title-item__pic{
            width: .75rem;
            height: auto;
        }
        .text-commission{
            color: #C16200;
        }
        .title-item__commission{
            margin-top: -.2rem;
        }
    }
    .tab{
        .tab-list{
            position: relative;
            &.two::before{
                left: auto;
                margin-left: calc(50%);
            }
            &::before{
                position: absolute;
                bottom: 0;
                left: calc(25% - 0.78rem);
                margin-left: 0;
                content: "";
                width: 1.56rem;
                height: 2px;
                background: #F1B90E;
                transition: all .3s;
            }
            .tab-list__item{
                width: 50%;
                height: .6rem;
                line-height: .6rem;
                transition: all .3s;
            }
        }
        .tab-content{
            border-radius: .1rem;
            overflow: hidden;
            .tab-content__list{
                border: 1px solid #545559;
                border-top: none;
                border-bottom-left-radius: .1rem;
                border-bottom-right-radius: .1rem;
            }
            .tab-content__title{
                background: #F1B90E;
                height: .48rem;
                line-height: .48rem;
                font-size: .16rem;
                color: #12110E;
            }
            .tab-list__one{
                flex: 3;
            }
            .tab-list__two{
                flex: 1;
            }
            .tab-content__item{
                padding: .2rem 0;
                border-bottom: 1px solid #3d445a;
                align-items: center;
            }
        }

        .tab-content{
            border-radius: .1rem;
            overflow: hidden;
            .tab-user{
                margin: 0 auto;
                width: 90%;
                max-width: 4.55rem;
                .tab-user__btn{
                    width: calc(50% - .25rem);
                    height: .4rem;
                    line-height: .4rem;
                    background: linear-gradient(-75deg, #F5BD02, #F29401);
                    border-radius: .06rem;
                }
                .tab-user__invitation{
                    background: #3D4151;
                    color: #fff;
                }
                .tab-user__text{
                    height: 2.26rem;
                    border: 1px solid #FFFFFF;
                    border-radius: .06rem;
                    width: 100%;
                    padding: .2rem;
                    >textarea{
                        background: none;
                        width: 100%;
                        height: 100%;
                        color: #AFAFB4;
                        resize: none;
                    }
                }
            }
            .tab-content__list{
                border: 1px solid #545559;
                border-top: none;
                border-bottom-left-radius: .1rem;
                border-bottom-right-radius: .1rem;
            }
            .tab-content__title{
                background: #F1B90E;
                height: .48rem;
                line-height: .48rem;
                font-size: .16rem;
                color: #12110E;
            }
            .tab-list__one{
                flex: 3;
            }
            .tab-list__two{
                flex: 1;
            }
            .tab-list__three{
                width: .4rem;
                height: .4rem;
            }
            .tab-content__item{
                padding: .2rem 0;
                border-bottom: 1px solid #3d445a;
                align-items: center;
            }
        }
    }
    
}
</style>